<template>
    <dev  style="width:50%;height:200px;float: left;" >
        <a-form :label-col="labelCol" :wrapper-col="wrapperCol" style="width:500px;height:200px;margin: auto;">
                <a-form-item label="/" required>
               <a-select
                v-model:value="modelRef.name"
                show-search
                placeholder="Select a person"
                style="width: 200px;    right: 26%;"
                :options="options"
                :filter-option="filterOption"
              ></a-select>

                </a-form-item>
                <a-form-item class="error-infos" :wrapper-col="{ span: 14, offset: 4 }">
                <a-button type="primary" @click.prevent="onSubmit"></a-button>
                <a-button style="margin-left: 10px" @click="resetFields"></a-button>
                </a-form-item>
        </a-form>
     </dev>
     <div  style="float: right;width:50%;">
        <div style="background: #ececec; padding: 30px ;width:350px;margin: auto;">
            <a-card title="" :bordered="false" style="width: 300px;text-align:left">
                <p>{{modelRef.mobleInfo.country_code}}</p>
                <p>{{modelRef.mobleInfo.international_format}}</p>
<!--            <p>{{modelRef.mobleInfo}}</p>-->
            </a-card>
        </div>
     </div>
</template>
